'use client';

import Image from 'next/image';
import { Card, CardContent, Typography, Chip, Button } from '@mui/material';
import type { Charity } from 'lib/charities';
import { colors } from '@/theme';
import { validateCharityUrl } from 'lib/charities';

interface CharityCardProps {
  charity: Charity;
}

const CharityCard: React.FC<CharityCardProps> = ({ charity }) => {
  const isDonationLinkValid = validateCharityUrl(charity.donateLink);
  const isWebsiteLinkValid = validateCharityUrl(charity.website);

  return (
    <Card className="flex gap-2 p-1 w-full" variant="outlined">
      {charity.image && (
        <Image
          src={`/images/foundations/${charity.image}`}
          alt={charity.title}
          width={120}
          height={120}
          className=" p-2 opacity-[0.92] saturate-[0.9] contrast-[0.95]"
        />
      )}
      <CardContent sx={{ flex: 1 }}>
        <Typography
          variant="body1"
          gutterBottom
          sx={{ color: colors.text.primary, fontWeight: 600 }}
        >
          {charity.title}
        </Typography>

        <Chip
          label={charity.category}
          size="small"
          sx={{
            mb: 1,
            backgroundColor: colors.pastel.purple.light,
            color: colors.white,
          }}
        />

        <Typography variant="body2" sx={{ mb: 1 }}>
          {charity.description}
        </Typography>
        {/* {isDonationLinkValid ? (
          <Button
            variant="contained"
            href={charity.donateLink}
            target="_blank"
            rel="noopener noreferrer"
            sx={{
              backgroundColor: colors.primary,
              textTransform: 'none',
              width: '100%',
              mb: 1,
              '&:hover': { backgroundColor: colors.pastel.green.dark },
            }}
          >
            Donate Now
          </Button>
        ) : (
          <Button
            variant="outlined"
            disabled
            sx={{
              width: '100%',
              textTransform: 'none',
            }}
          >
            Donation Link Unavailable
          </Button>
        )}

        {isWebsiteLinkValid && (
          <Button
            variant="text"
            href={charity.website}
            target="_blank"
            rel="noopener noreferrer"
            sx={{
              color: colors.primary,
              textTransform: 'none',
              fontSize: '0.9rem',
            }}
          >
            Visit Website â†’
          </Button>
        )} */}
      </CardContent>
    </Card>
  );
};

export default CharityCard;
